import styles from './FormsControls.module.css'
import {Field} from "redux-form";

export const FormControl = ({input, meta: {touched, error}, ...props}) => {
    const hasError = touched && error;
    return (
        <div className={styles.formControl + ' ' + (hasError ? styles.error : ' ')}>
            <div>
                <props.typeField {...input} {...props} />
            </div>
            {hasError && <span>{error}</span>}
        </div>
    )
}

export const createField = (placeholder, name, typeField, component, validate, type, text) =>
    <div>
        <Field placeholder={placeholder} name={name} typeField={typeField} component={component}
               validate={validate} type={type}/>{text}
    </div>