import React from "react";
import s from './MyPosts.module.css'
import Post from './Post/Post';
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, requiredField} from "../../../utils/validators/validators";
import {FormControl} from "../../common/FormsControls/FormsControls";

const MyPosts = React.memo(props => {
    let postsElements = props.postsData.map(p => (
        <Post message={p.message} likesCount={p.likesCount} key={p.id}/>))

    let onSubmitPosts = (formData) => {
        props.addPost(formData.postText);
    }

    return (
        <div className={s.postBlock}>
            <h3>my posts</h3>
            <PostsReduxForm onSubmit={onSubmitPosts}/>
            <div className={s.posts}>
                {postsElements}
            </div>
        </div>
    )
});

const maxLength10 = maxLengthCreator(10);

const PostsForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field component={FormControl} name='postText' typeField='textarea' placeholder='  ' validate={[requiredField, maxLength10]}/>
            <button>Add post</button>
        </form>
    )
}

const PostsReduxForm = reduxForm({
    form: 'ProfileAddNewPostForm'
})(PostsForm)


export default MyPosts;