import {createField, FormControl} from "../../common/FormsControls/FormsControls";
import {requiredField} from "../../../utils/validators/validators";
import style from './../../../components/common/FormsControls/FormsControls.module.css'

let LoginForm = ({handleSubmit, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            {createField('Login', 'email', 'input', FormControl, [requiredField], null)}
            {createField('Password', 'password', 'input', FormControl, [requiredField], 'password')}
            {createField(null, 'rememberMe', 'input', FormControl, null, 'checkbox', 'Remember me')}
            {error && <div className={style.formSummaryError}>
                {error}
            </div>}
            <div>
                <button>Log in</button>
            </div>
        </form>
    )
}

export default LoginForm;