import s from './Post.module.css'

const Post = (props) => {

    return (
        <div className={s.item}>
            <img src='https://thumb.tildacdn.com/tild6134-3731-4431-b130-336565363530/-/resize/506x/-/format/webp/_.png'></img>
            {props.message}
            <div className={s.like}>
                <span>likes = {props.likesCount}</span>
            </div>
        </div>

    )
}

export default Post;