const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
    dialogsData: [
        {id: 1, name: 'Shmoleg'},
        {id: 2, name: 'Geor'},
        {id: 3, name: 'Stray'},
        {id: 4, name: 'Haise'},
        {id: 5, name: 'Kaneki'},
    ],
    messagesData: [
        {id: 1, message: 'Hello'},
        {id: 2, message: 'How are you ?'},
        {id: 3, message: 'Fantastic !'}
    ]
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            let body = {
                id: 4,
                message: action.newMessageBody
            };
            return {
                ...state,
                messagesData: [...state.messagesData, body]
            }
        default:
            return state;
    }
}

export const sendMessageActionCreator = (newMessageBody) => ({
    type: SEND_MESSAGE,
    newMessageBody
})

export default dialogsReducer;