import BestFriendsItem from "./BestFriendsItem/BestFriendsItem";
import BestFriendsList from "./BestFriendsList";
import {connect} from "react-redux";

let mapStateToProps = (state) => {
    return {
        BestFriends: state.friendsPage.friendsNameData.map((el) => <BestFriendsItem name={el.name} key={el.id}/>)
    }
}

let mapDispatchToProps = () => {
    return {

    }
}

const BestFriendsListContainer = connect(mapStateToProps, mapDispatchToProps)(BestFriendsList);

export default BestFriendsListContainer;